header {
  width:100%;
  background-color: black;
  height:60px;
  display:flex;
  align-items: center;
  padding:10px 15px;
}
h1 {
  font-size:25px;
  font-weight:bold;
  color:white;
  margin:0;
}
.body {
  padding:0;
}
main {
  width:100%;
  box-sizing: border-box;
  padding:30px;
}
.change {
  display:block;
  width:500px;
  padding:10px;
  font-size:20px;
  margin-bottom:10px;
}
.check {
  padding:10px;
  display:flex;

}
.checks {
  margin-bottom:15px;
}
input[type='checkbox'] {
  display:block;
  padding:10px;
  margin-top:7px;
  margin-right:15px;
  
}
h2 {
  font-size:20px;
  font-weight:600;
  margin:0;
  transition: text-decoration 1s;
}
button {
  margin-right:10px !important;
}